@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Open Sans", "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1b1c1f;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.node {
  width: 25px;
  height: 25px;
  outline: 1px solid gray;
  display: inline-block;
}

.node-finish {
  background-color: #af3945;
}

.node-start {
  background-color: rgb(79, 145, 79);
}

.node-visited {
  -webkit-animation-name: visitedAnimation;
          animation-name: visitedAnimation;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgba(30, 57, 42, 0.75);
    border-radius: 100%;
    outline: 1px transparent;
  }

  50% {
    background-color: rgba(60, 196, 124, 0.75);
    outline: 1px transparent;
  }

  75% {
    transform: scale(1.2);
    background-color: rgba(77, 255, 207, 0.75);
    outline: 1px transparent;
  }

  100% {
    transform: scale(1);
    background-color: rgba(100, 252, 242, 0.75);
  }
}

@keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgba(30, 57, 42, 0.75);
    border-radius: 100%;
    outline: 1px transparent;
  }

  50% {
    background-color: rgba(60, 196, 124, 0.75);
    outline: 1px transparent;
  }

  75% {
    transform: scale(1.2);
    background-color: rgba(77, 255, 207, 0.75);
    outline: 1px transparent;
  }

  100% {
    transform: scale(1);
    background-color: rgba(100, 252, 242, 0.75);
  }
}

.node-wall {
  background-color: white;
}

.node-shortest-path {
  -webkit-animation-name: shortestPath;
          animation-name: shortestPath;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes shortestPath {
  0% {
    transform: scale(0.6);
    background-color: rgb(255, 254, 106);
    outline: 1px solid rgb(255, 254, 106);
  }

  50% {
    transform: scale(1.2);
    background-color: rgb(255, 254, 106);
    outline: 1px solid rgb(255, 254, 106);
  }

  100% {
    transform: scale(1);
    background-color: rgb(255, 254, 106);
  }
}

@keyframes shortestPath {
  0% {
    transform: scale(0.6);
    background-color: rgb(255, 254, 106);
    outline: 1px solid rgb(255, 254, 106);
  }

  50% {
    transform: scale(1.2);
    background-color: rgb(255, 254, 106);
    outline: 1px solid rgb(255, 254, 106);
  }

  100% {
    transform: scale(1);
    background-color: rgb(255, 254, 106);
  }
}

.grid {
  margin: 30px 0 0;
}

button {
  font-family: "Open Sans";
  font-weight: 500;
  font-size: 1.1rem;
  margin: 20px;
  padding: 5px 10px;
  background-color: rgba(100, 252, 242, 0.75);
  border: none;
  cursor: pointer;
}

a,
button:hover {
  background-color: rgba(102, 192, 186, 0.75);
}

.algorithmDataTable {
  padding: 1em 1em 5em 1em;
  display: flex;
  align-items: center;
  justify-content: center;
}

th {
  padding: 8px 2em 10px 2em;
  outline: 1px solid gray;
}

textarea {
  padding-bottom: 5rem;
  resize: none;
  border-radius: 0.35rem;
}

.gridIDTextArea {
  padding-bottom: 100px;
}

